.App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (min-width: 990px) {
    #testId{display: none;}
  }
  
  @media (min-width: 650px) {
    .brand-logo{font-size: 100%;}
  }

  @media (min-width: 300px) {
    .brand-logo{font-size: 75%;}
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  


@media screen and (min-width: 1130px) and (max-width: 400px) {
    .sideNav { display: none; }
  }
  
@media screen and (min-width: 401px) and (max-width: 1130px) {
    #menuList { display: none; } 
  }  
